'use client';

import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuLabel,
    DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { Button } from '@/components/ui/button';
import { MoreHorizontal, Pencil, Trash2, CreditCard, Globe, Hash, Shield } from 'lucide-react';
import type { Jugador } from '@/types';
import { Badge } from '@/components/ui/badge'; // Asegúrate de tener este componente, si no, usa un span con clases

interface JugadoresTableProps {
    jugadores: Jugador[];
    isLoading: boolean;
    onEliminar: (id: string) => void;
    onEditar: (id: string) => void;
}

// Helper para formatear RUT visualmente en la tabla
const formatRut = (rut: number | string, dv: string) => {
    if (!rut) return '-';
    const rutStr = rut.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return `${rutStr}-${dv}`;
};

export function JugadoresTable({ jugadores, isLoading, onEliminar, onEditar }: JugadoresTableProps) {

    if (isLoading) {
        return (
            <div className="p-8 text-center text-slate-500">
                Cargando listado de jugadores...
            </div>
        );
    }

    if (jugadores.length === 0) {
        return (
            <div className="p-8 text-center text-slate-500 border-t border-slate-100 dark:border-slate-800">
                No se encontraron jugadores con los filtros seleccionados.
            </div>
        );
    }

    return (
        <Table>
            <TableHeader>
                <TableRow className="bg-slate-50 dark:bg-slate-900/50 hover:bg-slate-50 dark:hover:bg-slate-900/50">
                    <TableHead className="w-[100px]">N°</TableHead>
                    <TableHead>Nombre Completo</TableHead>
                    <TableHead>Identificación</TableHead>
                    <TableHead>ROL</TableHead>
                    <TableHead>Club</TableHead>
                    <TableHead>Categoría</TableHead> {/* Opcional: Si tienes edad/año */}
                    <TableHead className="text-right">Acciones</TableHead>
                </TableRow>
            </TableHeader>
            <TableBody>
                {jugadores.map((jugador) => (
                    <TableRow key={jugador.id}>
                        {/* Camiseta */}
                        <TableCell className="font-medium">
                            <div className="flex items-center justify-center bg-slate-100 dark:bg-slate-800 h-8 w-8 rounded-full text-slate-600 dark:text-slate-300 font-bold text-xs">
                                {jugador.numero}
                            </div>
                        </TableCell>

                        {/* Nombre Completo */}
                        <TableCell>
                            <div className="flex flex-col">
                                <span className="font-medium text-slate-900 dark:text-slate-100">
                                    {jugador.paterno} {jugador.materno}
                                </span>
                                <span className="text-sm text-slate-500 dark:text-slate-400">
                                    {jugador.nombres}
                                </span>
                            </div>
                        </TableCell>

                        {/* COLUMNA UNIFICADA: Identificación */}
                        <TableCell>
                            {jugador.tipoIdentificacion === 'PASSPORT' ? (
                                <div className="flex items-center gap-2 text-indigo-600 dark:text-indigo-400" title="Pasaporte Extranjero">
                                    <Globe className="h-4 w-4" />
                                    <span className="font-mono text-sm uppercase">
                                        {jugador.pasaporte}
                                    </span>
                                </div>
                            ) : (
                                <div className="flex items-center gap-2 text-slate-600 dark:text-slate-300" title="RUT Nacional">
                                    <CreditCard className="h-4 w-4 text-slate-400" />
                                    <span className="font-mono text-sm">
                                        {formatRut(jugador.rut, jugador.dv)}
                                    </span>
                                </div>
                            )}
                        </TableCell>

                        {/* ROL */}
                        <TableCell>
                            <div className="flex items-center gap-1">
                                <Hash className="h-3 w-3 text-slate-400" />
                                <span className="text-sm text-slate-600 dark:text-slate-300">
                                    {jugador.rol || '-'}
                                </span>
                            </div>
                        </TableCell>

                        {/* Club */}
                        <TableCell>
                            {jugador.Club ? (
                                <Badge variant="outline" className="font-normal border-slate-200 dark:border-slate-700 bg-white dark:bg-slate-950">
                                    <Shield className="mr-1 h-3 w-3 text-blue-500" />
                                    {jugador.Club.nombre}
                                </Badge>
                            ) : (
                                <span className="text-slate-400 text-xs italic">Sin Club</span>
                            )}
                        </TableCell>

                        {/* Edad / Fecha (Opcional, calculada simple) */}
                        <TableCell>
                            <span className="text-sm text-slate-500">
                                {jugador.nacimiento ? new Date(jugador.nacimiento).getFullYear() : '-'}
                            </span>
                        </TableCell>

                        {/* Acciones */}
                        <TableCell className="text-right">
                            <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                    <Button variant="ghost" className="h-8 w-8 p-0">
                                        <span className="sr-only">Abrir menú</span>
                                        <MoreHorizontal className="h-4 w-4" />
                                    </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                    <DropdownMenuLabel>Acciones</DropdownMenuLabel>
                                    <DropdownMenuItem onClick={() => onEditar(jugador.id.toString())}>
                                        <Pencil className="mr-2 h-4 w-4" />
                                        Editar
                                    </DropdownMenuItem>
                                    <DropdownMenuItem
                                        className="text-red-600 focus:text-red-600 focus:bg-red-50 dark:focus:bg-red-900/10"
                                        onClick={() => onEliminar(jugador.id.toString())}
                                    >
                                        <Trash2 className="mr-2 h-4 w-4" />
                                        Eliminar
                                    </DropdownMenuItem>
                                </DropdownMenuContent>
                            </DropdownMenu>
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </Table>
    );
}